<template>
  <div class="exam">
    <h5>  {{exam_item.exam_desc}}:<time>{{exam_item.exam_time}}</time></h5>



    <el-form :inline="true" class="demo-form-inline" :loading="loading">

      <!--<el-form-item label="姓名">-->
        <!--<el-input v-model="search" placeholder="输入姓名查询"></el-input>-->
      <!--</el-form-item>-->
      <el-form-item>
        <el-button-group>
          <el-button  @click="add_form.visible=true" type="primary">增加监考老师</el-button>

        </el-button-group>

      </el-form-item>
      <el-form-item>

      </el-form-item>
    </el-form>


      <el-table
        :data="list"
        style="width: 100%"
        border
        size="small"
        v-loading="loading"

      >
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="teacher_name" label="监考老师"></el-table-column>

        <el-table-column prop="teacher_in_school_name" label="所属学校"></el-table-column>

        <el-table-column prop="exam_total" label="考试办总监考费"></el-table-column>

        <el-table-column prop="school_exam_total" label="本校总监考费"></el-table-column>
        <el-table-column prop="teacher_total" label="该老师总监考费"></el-table-column>
        <el-table-column  label="操作" width="135" >
          <template scope="scope">
            <el-button-group>
              <el-button type="danger" @click="remove(scope.row)" size="small">删除</el-button>
              <el-button @click="open_edit(scope.row)" size="small">编辑</el-button>
            </el-button-group>

          </template>
        </el-table-column>
      </el-table>


    <el-dialog title="增加考试信息" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">
        <el-form-item label="选择学校">
          <el-select v-model="active_school_id" placeholder="请选择">
            <el-option
              v-for="s in school_list"
              :key="s.id"
              :label="s.name"
              :value="s.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="选择老师">
          <el-select v-model="add_form.teacher" placeholder="请选择">
            <el-option
              v-for="t in teacher_list"
              :key="t.id"
              :label="t.name"
              :disabled="t.disabled"
              :value="t.id">
            </el-option>
          </el-select>
        </el-form-item>



        <el-form-item label="分配监考费">
          <el-input v-model="add_form.total" placeholder="输入监考老师"></el-input>
        </el-form-item>
      </el-form>


        <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="add" size="small">确 定</el-button>
        </span>
    </el-dialog>


    <el-dialog title="编辑考试信息" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">


        <el-form-item label="监考费用">
          <el-input v-model="edit_form.teacher_total" placeholder="输入监考费用"></el-input>
        </el-form-item>
      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="edit" size="small">确 定</el-button>
        </span>
    </el-dialog>

    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>
  import axios from 'axios'
  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'

export default {
  components: {ElButtonGroup},
  name: 'exam-teacher',
  data () {
    return {
      list:[],
      total:0,
      search:'',
      page_size:10,
      id:-1,
      current_page:1,
      loading:false,

      exam_item:{

      },

      add_form:{
        total:'',
        teacher:'',
        visible:false
      },
      edit_form:{

        teacher_total:'',
        visible:false
      },
      school_list:[

      ],
      teacher_list:[

      ],
      active_school_id:-1
    }
  },



  watch:{
      current_page(){
          this.fetch()
      },
    active_school_id(){
        this.fetch_teacher()
    }
  },

  methods:{
      change_page(p){
          console.log(p)
      },
      open_edit(data){
          console.log(data)
          data.visible = true
          this.edit_form = {
              visible:true,
              id:data.id,
              teacher_total:data.teacher_total
          }
      },
      async fetch(){

          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search

          this.loading = true
          var id = this.id
          var response = await request.get('/api/teacherexam/?schoolexam_id='+id)


          this.list = response.data.objects

          this.loading = false
      },
      async add(){
          var add_form = this.add_form
          add_form.school_exam = this.exam_item.id

          var response = await request.post('/api/teacherexam/',add_form)
          this.add_form.visible = false
          this.$message({
            type: 'success',
            message: '添加监考老师成功!'
          });
          this.fetch()
      },
      async remove(data){
        try {
          await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          var response = await request.delete('/api/teacherexam/'+data.id +'/')

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()


        }catch (e){
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        }

      },
      async fetch_school(){
        var response = await request.get('/api/school/')
        this.school_list = response.data.objects
        this.active_school_id = this.school_list[0].id
      },

      async fetch_teacher(){
        var active_school_id = this.active_school_id
        var response = await request.get('/api/teacher/?school_id='+active_school_id)
        var teacher = response.data.objects
        var that= this
        this.teacher_list = teacher.map(function (obj) {
          obj.disabled = false
          var exist = that.list.find(function (o) {
            return o.teacher_id == obj.id
          })
          if(exist){
            obj.disabled = true
          }
          return obj
        })
      },

      async edit(){
        var edit_form = this.edit_form

        var id = edit_form.id
        console.log(edit_form,'edit-form')
        var response = await request.put('/api/teacherexam/'+id + '/',edit_form)
        this.$message({
          type: 'success',
          message: '修改监考老师监考费成功!'
        });
        this.edit_form.visible = false
        this.fetch()
      },

      async fetch_exam(){
        var id = this.id
        var response = await request.get('/api/schoolexam/'+id + '/')
        this.exam_item = response.data

      }

  },
  async mounted(){
      this.id = this.$route.params.id
      await this.fetch_exam()
      await this.fetch()

      await this.fetch_school()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.exam{
  box-sizing:border-box;
  padding:20px;
}
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



