<template>
  <div class="teacher">
    <h5>   总计{{total}}条数据</h5>



    <el-form :inline="true" class="demo-form-inline" :loading="loading">
      <el-form-item>

      </el-form-item>

      <el-form-item label="姓名">
        <el-input v-model="search" placeholder="输入姓名查询" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" @click="fetch" size="small">查询</el-button>
          <el-button  @click="add_form.visible=true" size="small">增加老师</el-button>
          <el-button @click="upload_dialog.visible=true" size="small">批量上传</el-button>
        </el-button-group>
      </el-form-item>

    </el-form>


      <el-table
        :data="list"
        style="width: 100%"
        border
        size="mini"
        v-loading="loading"
      >

        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="name" label="名字"></el-table-column>
        <el-table-column prop="phone"  label="电话"></el-table-column>


        <el-table-column prop="idcard"  label="身份证"></el-table-column>
        <el-table-column prop="bankcard"  label="银行卡号"></el-table-column>
        <el-table-column prop="bankinfo"  label="银行信息"></el-table-column>
        <el-table-column  label="操作" width="135" >
          <template scope="scope">
            <el-button-group>
              <el-button type="danger" @click="remove(scope.row)" size="small">删除</el-button>
              <el-button @click="open_edit(scope.row)" size="small">编辑</el-button>

            </el-button-group>

          </template>
        </el-table-column>
      </el-table>


    <el-dialog title="增加老师信息" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">
        <el-form-item label="姓名">
          <el-input v-model="add_form.name" placeholder="输入姓名"></el-input>
        </el-form-item>

        <el-form-item label="电话">
          <el-input v-model="add_form.phone" placeholder="输入电话"></el-input>
        </el-form-item>



        <el-form-item label="身份证">
          <el-input v-model="add_form.idcard" placeholder="输入身份证"></el-input>
        </el-form-item>

        <el-form-item label="银行卡号">
          <el-input v-model="add_form.bankcard" placeholder="银行卡号"></el-input>
        </el-form-item>
        <el-form-item label="银行信息">
          <el-input v-model="add_form.bankinfo" placeholder="银行卡号"></el-input>
        </el-form-item>
      </el-form>


        <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="add" size="small">确 定</el-button>
        </span>
    </el-dialog>


    <el-dialog title="编辑老师信息" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline"  label-width="100px">


        <el-form-item label="姓名">
          <el-input v-model="edit_form.name" placeholder="输入姓名"></el-input>
        </el-form-item>

        <el-form-item label="电话">
          <el-input v-model="edit_form.phone" placeholder="输入电话"></el-input>
        </el-form-item>



        <el-form-item label="身份证">
          <el-input v-model="edit_form.idcard" placeholder="输入身份证"></el-input>
        </el-form-item>

        <el-form-item label="银行卡号">
          <el-input v-model="edit_form.bankcard" placeholder="银行卡号"></el-input>
        </el-form-item>
        <el-form-item label="银行信息">
          <el-input v-model="edit_form.bankinfo" placeholder="银行信息"></el-input>
        </el-form-item>
      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>

    <el-dialog title="批量上传老师信息" :visible.sync="upload_dialog.visible"
               size="small"


    >
      <el-row>
        <el-button size="small">下载模板文件</el-button>
      </el-row>
      <br/>



      <el-upload
        class="upload-demo"
        drag
        style="width: 100%"
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>


      <span slot="footer" class="dialog-footer">
          <el-button @click="upload_dialog.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>



    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";

export default {
  components: {
    ElButton,
    ElButtonGroup},
  name: 'teacher-info',
  data () {
    return {
      school_id:window.school_id,
      list:[],
      total:0,
      search:'',
      page_size:10,
      current_page:1,
      loading:false,
      add_form:{
        id:'',
        name:'',
        phone:'',

        idcard:'',
        bankcard:'',
        bankinfo:'',
        visible:false
      },
      edit_form:{
        id:'',
        name:'',
        phone:'',
        idcard:'',
        bankcard:'',
        bankinfo:'',
        visible:false
      },
      upload_dialog:{
        visible:false
      }
    }
  },

  watch:{
      current_page(){
          this.fetch()
      }
  },

  methods:{
      change_page(p){
          console.log(p)
      },
      open_edit(data){
          console.log(data)
          data.visible = true
          this.edit_form = {
              visible:true,
              id:data.id,
              name:data.name,
              phone:data.phone,
              bankinfo:data.bankinfo,
              idcard:data.idcard,
              bankcard:data.bankcard,
              school_id:data.school_id
          }
      },
      async fetch(){

          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search,
              that = this

          this.loading = true
          console.log(window.school,'window.school')
          var response = await request.get('/api/teacher/?school_id='+window.school.id)
          this.list = response.data.objects

          this.loading = false
      },
      async add(){
          var add_form = this.add_form
          add_form.school_id = this.school_id
          delete add_form.visible
          var response = await request.post('/api/teacher/',add_form)
          this.fetch()
      },
      async remove(data){
        try {
          await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          var response = await request.delete('/api/teacher/'+data.id+'/')

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
           this.fetch()
        }catch (e){
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        }

      },
      async edit(){
        var edit_form = this.edit_form
        console.log(edit_form)
        var id = edit_form.id

        this.$message({
          type: 'success',
          message: '修改信息成功!'
        });

        var response = await request.put('/api/teacher/'+id +'/',edit_form)
        this.edit_form.visible = false
        this.fetch()
      }
  },
  mounted(){
      this.fetch()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
h1, h2 {
  font-weight: normal;
}
.teacher{
  box-sizing:border-box;
  padding:20px;
}
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
  .el-upload{
    display: block ;
  }
  .el-upload-dragger{
    width: 100% ;
  }
</style>



